import DPlayer from 'dplayer';
import {colors} from 'quasar'

export default {
  name: "dplayer",
  components: {},
  props: {
    topic_data: undefined,
    show_title: undefined,
    embed: undefined,
  },
  data() {
    return {
      show: true,
      player: undefined,
      video_ele: undefined,
      video_percent: undefined,
      video_status: undefined,
      online_video: undefined,
      //选择的线路
      select_server_name: undefined,
      show_touch_play_progress_flag: false,
      touch_play_time: "",
      touch_current_play_time: "",

    }
  },
  created() {
    this.select_server_name = localStorage.getItem('select_server_name');
    if (!this.select_server_name) {
      this.select_server_name = this.$config.defalut_video_server;
    }
    console.log('watch_video create');
    this.init();

  },
  destroyed() {
    console.log('dplayer destroyed');
  },
  computed: {
    is_jav_video: function () {
      var video_info = this.topic_data.video_info;
      return video_info['av_data_type'] === 2;
    },
    is_show_video_percent: function () {
      if (isNaN(this.video_percent) || !this.video_status) {
        return false;
      }
      if (this.video_status == 'playing' || this.video_status == 'pause' || this.video_status == 'canplay') {
        return false;
      }
      return true;
    },
  },
  watch: {
    select_server_name(newVal) {
      console.log('[dplayer]select_server_name change:', newVal);
      this.on_switch_server(newVal);
    }
  },
  methods: {
    init: function () {
      this.online_video = this.topic_data.video_info.online_videos[0];
      this.$nextTick(this.init_video_player);
      window._t = this;
    },
    init_video_player: function () {

      if (this.is_ios()) {
        this.player = document.querySelector('#orginal_video_player');
        this.player.addEventListener('loadeddata', function () {
          // Video is loaded and can be played
          console.log('on loadeddata');
        }, false);
      } else {

        if (!window.Hls) {
          console.log('not hls');
          return setTimeout(this.init_video_player, 100);
        }

        if (window.Hls && window.Hls.DefaultConfig) {
          window.Hls.DefaultConfig.maxBufferLength = 120;
          window.Hls.DefaultConfig.maxBufferSize = 10e7;
        }


        var volume = localStorage.getItem('volume');
        if (volume) {
          volume = parseInt(volume);
        } else {
          volume = 0.5;
        }
        var option = {
          // 配置参数
          container: document.getElementById('video-player-container'),
          autoplay: false,
          theme: colors.getBrand('primary'),
          loop: true,
          lang: 'zh-cn',
          preload: 'auto',
          volume: volume,
          video: {
            url: this.get_m3u8_url(),
            pic: this.get_cover_image_url(),
            type: 'customHls',
            customType: {
              customHls: function (video, player) {
                const hls = new window.Hls();
                hls.loadSource(video.src);
                hls.attachMedia(video);

                hls.on(window.Hls.Events.FRAG_LOAD_PROGRESS, (event, {frag, stats}) => {
                  var percent = parseInt((stats.loaded / stats.total) * 100);
                  console.log('percent:', percent);
                  if (!isNaN(percent)) {
                    this.video_percent = percent;
                  }
                });

              },
            },
          },
          /*contextmenu: [
            {
              text: 'custom1',
              link: 'https://github.com/DIYgod/DPlayer',
            },
            {
              text: 'custom2',
              click: (player) => {
                console.log(player);
              },
            },
          ],*/
        };

        if (document.querySelector("#video-player-container") && document.querySelector("#video-player-container").oncontextmenu) {
          document.querySelector('.dplayer-menu')
        }

        console.log('option:', option);

        this.player = new DPlayer(option);

        // 禁止右键下载视频
        document.oncontextmenu = new Function("event.returnValue=false;");
        document.onselectstart = new Function("event.returnValue=false;");

        // 修改循环播放显示
        document.getElementsByClassName('dplayer-setting-item dplayer-setting-loop')[0].getElementsByClassName('dplayer-label')[0].innerText = "循环播放"
        // 修改倍速播放显示
        document.getElementsByClassName('dplayer-setting-item dplayer-setting-speed')[0].getElementsByClassName('dplayer-label')[0].innerText = "播放倍速"

        this.add_touch();

        this.player.on('volumechange', (e) => {

          console.log('volumechange event:', e);
          localStorage.setItem('volume', this.player.volume());
        });

        this.player.on('waiting', (e) => {
          console.log('waiting event:', e);
        });

        this.player.on('playing', (e) => {
          console.log('playing event:', e);
        });

        var plyr_ele = document.querySelector('.play-root');
        plyr_ele.appendChild(this.$refs['touch_play_progress']);
        plyr_ele.appendChild(this.$refs['video_percent']);


        (function () {
          function R(a) {
            var ona = "on" + a;
            if (window.addEventListener) {
              window.addEventListener(a, function (e) {
                for (var n = e.originalTarget; n; n = n.parentNode) {
                  n[ona] = null;
                }
              }, true);
            }
            window[ona] = null;
            document[ona] = null;
            if (document.body) {
              document.body[ona] = null;
            }
          }

          R("contextmenu");
          R("click");
          R("mousedown");
          R("mouseup");
          R("selectstart");
        })();

      }

    },
    get_cover_image_url: function () {
      var cover_image_url = this.generate_cover_url(this.online_video.space_name, this.online_video.hash_id);
      console.log("cover_image_url:", cover_image_url);
      return cover_image_url;
    },
    get_m3u8_url: function () {
      var m3u8_url = "/video/m3u8/" + this.topic_data.video_info.online_videos[0].hash_id + ".m3u8" + "?video_server=" + this.select_server_name;
      var host = window.location.host;
      if (host.indexOf('localhost') >= 0) {
        m3u8_url = "https://" + this.$debug_host + m3u8_url;
      }
      return m3u8_url;
    },
    on_switch_server: function (server_name) {
      this.select_server_name = server_name;
      localStorage.setItem('select_server_name', this.select_server_name);
      this.show = false;
      this.$nextTick(() => {
        this.show = true;
        this.init();
        this.$nextTick(() => {

          var auto_play = () => {
            if (!this.player) {
              setTimeout(this.auto_play, 200);
            } else {
              this.player.play();
            }
          }
          auto_play();
        });
        if (this.$ga) {
          this.$ga.event({
            eventCategory: 'on_switch_server',
            eventAction: 'switch',
            eventLabel: this.select_server_name,
            eventValue: this.$store.state.hasLogin ? "1" : "2"
          });
        }
      });
    },
    add_touch: function () {

      console.log('call add_touch');

      if (!window.Hammer) {
        return setTimeout(this.add_touch, 500);
      }

      function paddingZero(num, length) {
        return (num / Math.pow(10, length)).toFixed(length).substr(2);
      }

      const defaultImplementation = function (seconds, guide) {
        seconds = seconds < 0 ? 0 : seconds;
        let s = Math.floor(seconds % 60);
        let m = Math.floor(seconds / 60 % 60);
        let h = Math.floor(seconds / 3600);
        const gm = Math.floor(guide / 60 % 60);
        const gh = Math.floor(guide / 3600);

        if (isNaN(seconds) || seconds === Infinity) {
          h = m = s = '-';
        }

        h = (h > 0 || gh > 0) ? h : '0';

        m = (((h || gm >= 10) && m < 10) ? '0' + m : m);

        s = (s < 10) ? '0' + s : s;

        return paddingZero(h, 2) + ":" + paddingZero(m, 2) + ":" + paddingZero(s, 2);
      };
      let implementation = defaultImplementation;

      function formatTime(seconds, guide = seconds) {
        return implementation(seconds, guide);
      }

      var show_touch_play_progress = (deltaX) => {
        var duration = this.player.duration;
        var currentTime = this.player.currentTime;

        var move_time = currentTime + (deltaX / document.body.clientWidth) * (60 * 7);

        if (move_time > duration) {
          move_time = duration;
        }
        if (move_time < 0) {
          move_time = 0;
        }

        console.log('[show_touch_play_progress]:', duration);
        this.show_touch_play_progress_flag = true;
        this.touch_current_play_time = formatTime(duration);
        this.touch_play_time = formatTime(move_time);
        return move_time;
      };


      var hide_touch_play_progress = () => {
        //jQuery('#touch-play-progress').hide();
        this.show_touch_play_progress_flag = false;
      };


      var init_touch_function = () => {
        //var progress_template = jQuery('#touch-play-progress-template').html();
        //jQuery('#video-player-container div:first').append(progress_template);

        var current_move_time = undefined;
        var hammertime = new window.Hammer(document.getElementById('video-player-container'));

        hammertime.on('pan', (ev) => {
          console.log("pan event:", ev);
          if (ev.additionalEvent == "panright" && ev.deltaX > 35) {
            current_move_time = show_touch_play_progress(ev.deltaX);
          }
          if (ev.additionalEvent == "panleft" && ev.deltaX < -35) {
            current_move_time = show_touch_play_progress(ev.deltaX);
          }
        });

        hammertime.on('panend', (ev) => {
          hide_touch_play_progress();
          if (current_move_time) {
            this.player.currentTime = current_move_time;
            this.player.play();
            current_move_time = undefined;
          }
        });

        hammertime.on('pancancel', () => {
          hide_touch_play_progress();
        });
      };

      init_touch_function();
    }
  }
}
